.wp-block-blockylicious-curvy {
    position: relative;
    background: #21759b;
    padding: 80px 50px;

    .curve {
        position: absolute;
        left: 0;
        width: 100%;
        overflow: hidden;

        &.top-curve {
            top: 0;
        }

        &.bottom-curve {
            bottom: 0;
        }

        svg {
            position: absolute;
            top: 0;
            left: 0;
        }
    }

}
